import React, { useState } from "react";
import {
  Ribbon,
  RibbonGroup,
  RibbonGroupItem,
  RibbonButton,
} from "react-bootstrap-ribbon";
import "bootstrap/dist/css/bootstrap.css";
import "react-bootstrap-ribbon/dist/react-bootstrap-ribbon.css";

function RibbonView(props) {
  const onProject = () => {
    props.setProject(true);
  };

  const onShip = () => {
    props.setShip(true);
  };
  const onEdit = () => {
    props.setEdit(true);
  };

  return (
    <div className="RibbonView">
      <Ribbon breakpoint="lg" height="6rem">
        <RibbonGroup title="Ship Model" colClass="col-3">
          <RibbonGroupItem colClass="col-4">
            <RibbonButton onClick={onProject}>
              <svg
                className="bi bi-clipboard"
                width="1.3em"
                height="1.3em"
                viewBox="0 0 16 16"
                fill="currentColor"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  fillRule="evenodd"
                  d="M4 1.5H3a2 2 0 00-2 2V14a2 2 0 002 2h10a2 2 0 002-2V3.5a2 2 0 00-2-2h-1v1h1a1 1 0 011 1V14a1 1 0 01-1 1H3a1 1 0 01-1-1V3.5a1 1 0 011-1h1v-1z"
                  clipRule="evenodd"
                />
                <path
                  fillRule="evenodd"
                  d="M9.5 1h-3a.5.5 0 00-.5.5v1a.5.5 0 00.5.5h3a.5.5 0 00.5-.5v-1a.5.5 0 00-.5-.5zm-3-1A1.5 1.5 0 005 1.5v1A1.5 1.5 0 006.5 4h3A1.5 1.5 0 0011 2.5v-1A1.5 1.5 0 009.5 0h-3z"
                  clipRule="evenodd"
                />
              </svg>

              <div>Start Project</div>
            </RibbonButton>
          </RibbonGroupItem>
          <RibbonGroupItem colClass="col-4">
            <RibbonButton onClick={onShip}>
              <svg
                className="bi bi-files"
                width="1.3em"
                height="1.3em"
                viewBox="0 0 16 16"
                fill="currentColor"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  fillRule="evenodd"
                  d="M3 2h8a2 2 0 012 2v10a2 2 0 01-2 2H3a2 2 0 01-2-2V4a2 2 0 012-2zm0 1a1 1 0 00-1 1v10a1 1 0 001 1h8a1 1 0 001-1V4a1 1 0 00-1-1H3z"
                  clipRule="evenodd"
                />
                <path d="M5 0h8a2 2 0 012 2v10a2 2 0 01-2 2v-1a1 1 0 001-1V2a1 1 0 00-1-1H5a1 1 0 00-1 1H3a2 2 0 012-2z" />
              </svg>
              <div style={{ fontSize: "15px" }}>Add Ship</div>
            </RibbonButton>
          </RibbonGroupItem>

          <RibbonGroupItem colClass="col-4">
            <RibbonButton onClick={onEdit}>
              <svg
                className="bi bi-pencil"
                width="1.3em"
                height="1.3em"
                viewBox="0 0 16 16"
                fill="currentColor"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  fillRule="evenodd"
                  d="M11.293 1.293a1 1 0 011.414 0l2 2a1 1 0 010 1.414l-9 9a1 1 0 01-.39.242l-3 1a1 1 0 01-1.266-1.265l1-3a1 1 0 01.242-.391l9-9zM12 2l2 2-9 9-3 1 1-3 9-9z"
                  clipRule="evenodd"
                />
                <path
                  fillRule="evenodd"
                  d="M12.146 6.354l-2.5-2.5.708-.708 2.5 2.5-.707.708zM3 10v.5a.5.5 0 00.5.5H4v.5a.5.5 0 00.5.5H5v.5a.5.5 0 00.5.5H6v-1.5a.5.5 0 00-.5-.5H5v-.5a.5.5 0 00-.5-.5H3z"
                  clipRule="evenodd"
                />
              </svg>
              <div style={{ fontSize: "15px" }}>Edit Property</div>
            </RibbonButton>
          </RibbonGroupItem>
        </RibbonGroup>
      </Ribbon>
    </div>
  );
}

export default RibbonView;
