import React, { useRef, useState } from "react";
import styles from "../css/ShipProp.module.css";

export default function ShipProp(props) {
  const [type, SetType] = useState(props.type);

  let ph = "";
  let rv = false;
  const inputRef = useRef(null);
  if (type === "Width") {
    rv = true;
    ph = `m`;
  } else if (type === "Depth") {
    rv = true;
    ph = `m`;
  } else if (type === "Height") {
    rv = true;
    ph = `m`;
  } else if (type === "Global Ship Position") {
    ph = `[   ,   ,   ]`;
    rv = true;
  } else if (type === "Weight") {
    rv = true;
    ph = `ton`;
  } else if (type === "Underwater Volume") {
    rv = true;
    ph = `m³`;
  } else if (type === "Ship Attitude") {
    rv = true;
    ph = `[   ,   ,   ]`;
  } else if (type === "Local COG") {
    rv = true;
    ph = `[   ,   ,   ]`;
  } else if (type === "Global COG") {
    rv = true;
    ph = `[   ,   ,   ]`;
  } else if (type === "Local COB") {
    rv = true;
    ph = `[   ,   ,   ]`;
  } else if (type === "Global COB") {
    rv = true;
    ph = `[   ,   ,   ]`;
  } else if (type === "Draft") {
    rv = true;
    ph = `m`;
  } else if (type === "Heel") {
    rv = true;
    ph = `°`;
  } else if (type === "Trim") {
    ph = `°`;
    rv = true;
  }

  const onSubmit = (event) => {
    event.preventDefault();
  };

  if (props.edit === true) {
    if (type === "Width") {
      let tempShipInfo = props.shipInfo;
      if (inputRef.current.value !== "") {
        tempShipInfo.width = parseFloat(inputRef.current.value);
        props.setShipInfo(tempShipInfo);
      }
    } else if (type === "Depth") {
      let tempShipInfo = props.shipInfo;
      if (inputRef.current.value !== "") {
        tempShipInfo.depth = parseFloat(inputRef.current.value);
        props.setShipInfo(tempShipInfo);
      }
    } else if (type === "Height") {
      let tempShipInfo = props.shipInfo;
      if (inputRef.current.value !== "") {
        tempShipInfo.height = parseFloat(inputRef.current.value);
        props.setShipInfo(tempShipInfo);
      }
    } else if (type === "Global Ship Position") {
      let tempShipInfo = props.shipInfo;
      if (inputRef.current.value !== "") {
        tempShipInfo.globalShipPosition = inputRef.current.value;
        props.setShipInfo(tempShipInfo);
      }
    } else if (type === "Weight") {
      let tempShipInfo = props.shipInfo;
      if (inputRef.current.value !== "") {
        tempShipInfo.weight = parseFloat(inputRef.current.value);
        props.setShipInfo(tempShipInfo);
      }
    } else if (type === "Ship Attitude") {
      let tempShipInfo = props.shipInfo;
      if (inputRef.current.value !== "") {
        tempShipInfo.shipAttitude = inputRef.current.value;
        props.setShipInfo(tempShipInfo);
      }
    }
  }
  return (
    <div className={styles.box}>
      {`${props.type} : `}
      <form onSubmit={onSubmit}>
        {rv ? (
          <input
            ref={inputRef}
            style={{
              width: "4.6rem",
              height: " 1.2rem",
              textAlign: "right",
            }}
            placeholder={ph}
          ></input>
        ) : (
          <input
            ref={inputRef}
            style={{
              width: "4.6rem",
              height: " 1.2rem",
              textAlign: "right",
            }}
            disabled
            placeholder={ph}
          ></input>
        )}
      </form>
    </div>
  );
}
