import { OrbitControls } from "@react-three/drei";
import React, { useRef } from "react";

function Cube(props) {
  const mesh = useRef();
  if (props.projects.length === 0) {
    return <></>;
  }

  if (typeof mesh.current !== "undefined") {
    let arrGlobalShipPosition = props.shipInfo.globalShipPosition
      .split(",")
      .map((element) => parseFloat(element));

    let arrShipAttitude = props.shipInfo.shipAttitude
      .split(",")
      .map((element) => parseFloat(element));
    mesh.current.position.x = arrGlobalShipPosition[0];
    mesh.current.position.y = arrGlobalShipPosition[1];
    mesh.current.position.z = arrGlobalShipPosition[2];

    mesh.current.rotation.x = arrShipAttitude[0];
    mesh.current.rotation.y = arrShipAttitude[1];
    mesh.current.rotation.z = arrShipAttitude[2];
  }

  return (
    <mesh ref={mesh}>
      <boxBufferGeometry
        args={[
          props.shipInfo.width,
          props.shipInfo.height,
          props.shipInfo.depth,
        ]}
      />
      <meshPhongMaterial color="skyblue" opacity={0.7} transparent />
      <OrbitControls></OrbitControls>
    </mesh>
  );
}

export default Cube;
