import React from "react";
import TreeView from "@mui/lab/TreeView";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import ChevronRightIcon from "@mui/icons-material/ChevronRight";
import TreeItem from "@mui/lab/TreeItem";
import axios from "axios";

export default function MultiSelectTreeView(props) {
  if (props.project === true) {
    props.setProjects((current) => [...current, [true]]);
    props.setProject(false);
  }

  if (props.ship === true) {
    if (props.projectNumber !== 0) {
      let tempProjects = props.projects;
      let tempShips = props.projects[props.projectNumber - 1];
      tempShips.push(true);
      tempProjects[props.projectNumber - 1] = tempShips;
      props.setProjects(tempProjects);
      props.setShip(false);
    }
  }

  const onProject = (e) => {
    let tempObj = Object.values(e.target)[1];

    if ("children" in tempObj) {
      let pN = parseInt(tempObj.children.split(" ")[1]);
      props.setProjectNumber(parseInt(pN));
    } else {
      props.setProjectNumber(101);
    }
  };

  const onShip = (e) => {
    let numbers = e.nativeEvent.path[6].innerText.split("\n");
    let pN = parseInt(numbers[0].split(" ")[1]);
    let sN = parseInt(e.target.innerText[e.target.innerText.length - 1]);

    props.setShipNumber(parseInt(sN));
    props.setProjectNumber(parseInt(pN));
    let tempShipInfo = props.shipInfo;
    tempShipInfo.shipId = parseInt(100 * pN + sN);
    props.setShipInfo(tempShipInfo);

    console.log(props.shipInfo.shipId);
    axios
      .get(`http://127.0.0.1:8000/${props.shipInfo.shipId}`)
      .then((response) => {
        props.setShipInfo(response.data);
        console.log(response.data);
      })
      .catch(function (error) {
        console.log(error);
      });
  };

  return (
    <div
      style={{
        height: "100vh",
        width: "9vw",
        maxWidth: "20vw",
        backgroundColor: "#1c1b22",
        color: "white",
        position: "absolute",
      }}
    >
      <TreeView
        aria-label="multi-select"
        defaultCollapseIcon={<ExpandMoreIcon />}
        defaultExpandIcon={<ChevronRightIcon />}
        multiSelect
        sx={{
          height: "100vh",
          flexGrow: 1,
          maxWidth: "10vw",
          overflowY: "auto",
        }}
      >
        {props.projects.map((element, index) => (
          <TreeItem
            nodeId={String(index + 1)}
            key={index + 1}
            label={`Project ${index + 1}`}
            onClick={onProject}
          >
            {element.map((inner, innerindex) => (
              <TreeItem
                nodeId={`${index + 1}-${innerindex + 1}`}
                key={`${index + 1}-${innerindex + 1}`}
                label={`Ship ${innerindex + 1}`}
                onClick={onShip}
              ></TreeItem>
            ))}
          </TreeItem>
        ))}
        {/* <TreeItem nodeId="1" label="Project1">
          <TreeItem nodeId="2" label="Ship1" />
          <TreeItem nodeId="3" label="Ship2" />
          <TreeItem nodeId="4" label="Ship3" />
        </TreeItem>
        <TreeItem nodeId="5" label="Project2">
          <TreeItem nodeId="6" label="Ship"></TreeItem>
        </TreeItem> */}
      </TreeView>
    </div>
  );
}
