function Sphere(props) {
  if (props.projects.length === 0) {
    return <></>;
  }
  let arrPosition = props.position.split(",");
  return (
    <mesh position={arrPosition}>
      <sphereBufferGeometry args={[0.2, 20, 20]} />
      <meshPhongMaterial color={props.color} />
    </mesh>
  );
}

export default Sphere;
